body {
  margin: 0;
  padding: 0;
  background-color: #96bdb9;
  font-family: sans-serif;
  height: 300px;
}
.header {
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
}
.questionContainer {
  display: flex;
  justify-content: center;
  margin-top: 8pxpx;
}
.anserContainer {
  display: flex;
  justify-content: center;
  margin-top: 2px;
}
.question {
  text-align: center;
  display: inline-block;
  line-height: 150px;
  height: 150px;
  width: 150px;
  background-color: rgb(89, 43, 163);
  color: white;
  font-size: 25px;
  border: 2px solid rgb(10, 12, 10);
  border-radius: 5px;
}

.anser {
  text-align: center;
  display: inline-block;
  line-height: 150px;
  width: 150px;
  height: 150px;
  background-color: rgb(218, 129, 221);
  color: white;
  font-size: 100px;
  border: 2px solid rgb(10, 12, 10);
  border-radius: 5px;
}

.start {
  color: rgb(218, 129, 221);
}

.anserContainer {
  background-color: #96bdb9;
  display: flex;
  justify-content: center;
  margin-top: 4px;
  height: 50px;
}

input {
  width: 224px;
  font-size: 30px;
  border: 2px solid rgb(10, 12, 10);
  border-radius: 5px;
}
button {
  width: 70px;
  font-size: 11px;
  border: 2px solid rgb(10, 12, 10);
  border-radius: 5px;
}
